(function() {
  'use strict';

  angular
    .module('pasturaboscEditor')
    .directive('acmeFooter', acmeFooter);

  /** @ngInject */
  function acmeFooter() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/footer/footer.html',
      scope: {
          relativeDate: '='
      },
      controller: FooterController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function FooterController($scope, BootSwatchService) {
		$scope.themes = [];
		
		activate();
		
		function activate() {
			getThemes();
		}
		
		function getThemes() {
			/*Get the list of availabel bootswatch themes*/
			BootSwatchService.get().then(function(themes) {
				$scope.themes = themes;
				$scope.themes.unshift({name:'Default',css:''});
			});
		}  
    }
  }

})();

'use strict';

angular.module('pasturaboscEditor')
    .factory('BootSwatchService', function ($http) {
        return {
            get: function() {
                return $http.get('http://bootswatch.com/api/3.json').then(function (response) {
                    return response.data.themes;
                });
            }
        };
    });
'use strict';

angular.module('pasturaboscEditor')
    .directive('jhSwitchTheme', function() {
        /*Directive binds to anchor to update the bootswatch theme selected*/
        return {
            restrict: 'A',
            scope: {
                theme : '=jhSwitchTheme'
            },
            link: function (scope, element) {
                var currentTheme = angular.element("#bootswatch-css").attr('title');
                if(scope.theme.name === currentTheme){
                    element.parent().addClass("active");
                }

                element.on('click',function(){
                    angular.element("#bootswatch-css").attr("href", scope.theme.css);
                    angular.element(".theme-link").removeClass("active");
                    element.parent().addClass("active");
                });
            }
        };
    });
(function() {
  'use strict';

  angular
    .module('pasturaboscEditor')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController(news, $log, toastr, services, methodologies, recipes) {
    var vm = this;
	vm.newsThings = [];
	vm.newsEdit = {};
	vm.newsThingFilter = {};
	vm.languageOptions = [];
	vm.selected = {};
	
	var newsService = news;

    activate();

    function activate() {
		loadLanguageOptions();
		getNewsThings();
		angular.element('#comment').wysihtml5({"html":true});
		resetNewsEdit();
		angular.element('#datetimepicker1').datetimepicker({
                format: 'DD/MM/YYYY'
        });
	}

    function resetNewsEdit() {
		vm.newsEdit = {
			"id": 0,
			"language": "ca",
			"image":"",
			"url":"",
			"date":"",
			"title":"",
			"description":"",
			"text":""
		};
		angular.element('#comment').data("wysihtml5").editor.setValue("");
    }	
	
    function getNewsThings() {
		newsService.getNews().then(function(data) {
			vm.newsThings = data;
		})
		.catch(function(err) {
			$log.error(err);
        });
    }	
	
	vm.loadNewsThing = function (id) {
		$log.debug('loadNewsThing id:' + id);
		for(var i = 0; i<vm.newsThings.length;i++) {
			if (vm.newsThings[i].id === id) {
				vm.newsEdit = vm.newsThings[i];
				angular.element('#comment').data("wysihtml5").editor.setValue(vm.newsEdit.text);
				break;
			}
		}
	}
	
	vm.remove = function (id) {
		for(var i = 0; i<vm.newsThings.length;i++) {
			if (vm.newsThings[i].id === id) {
				vm.newsThings.splice(i, 1);
				exportSaveJSONLink();
				toastr.success('Content removed correctly');
				resetNewsEdit();
				break;
			}
		}
	}
	
	vm.reset = function () {
		resetNewsEdit();
	}
	
	vm.save = function (newsEdited) {
		$log.debug('save newsEdited');
		newsEdited.text = angular.element('#comment').val();
		if(newsEdited.id === 0) {
			newsEdited.id = vm.newsThings.length +1;
			vm.newsThings.push(newsEdited);
			toastr.success('Content created correctly');
		} else {
			for(var i = 0; i<vm.newsThings.length;i++) {
				if (vm.newsThings[i].id === newsEdited.id) {
					vm.newsThings[i] = newsEdited;
					toastr.success('Content updated correctly');
					break;
				}
			}
		}
		exportSaveJSONLink();
		$log.debug(newsEdited);
	}
	
	function exportSaveJSONLink() {
		angular.element("#saveJsonContainer").html("");
		var data = "text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(vm.newsThings));
		var fileName = newsService.getNewsFileName();
		angular.element('<a href="data:' + data + '" download="'+fileName+'">Exportar '+fileName+'</a>').appendTo('#saveJsonContainer');
	}
	
	function loadLanguageOptions() {
		vm.languageOptions = [
			{
				field: "ca",
				title: "Català"
			},
			{ 
				field:"es",
				title:"Castellà"
			},
			{
				field:"en",
				title:"Angles"
			},
			{
				field: "fr",
				title: "Frances"
			}
		];
		vm.selected = vm.languageOptions[0].field;
		vm.newsThingFilter.language = vm.selected;
	}
	
	vm.changeNewsLanguage = function() {
		vm.newsThingFilter.language = vm.selected;
		resetNewsEdit();
	}
	
	vm.changeService = function(serviceId) {
		resetNewsEdit();
		angular.element("#saveJsonContainer").html("");
		if(serviceId === 3) {
			newsService = methodologies;
		} else if(serviceId === 2) {
			newsService = recipes;
		} else if(serviceId === 1) {
			newsService = services;
		} else {
			newsService = news;
		}
		getNewsThings();
	}
	
	/*
	vm.saveJSON = function () {
		$log.debug('saveJSON');
		vm.toJSON = '';
		vm.toJSON = angular.toJson(vm.newsThings);
		var blob = new Blob([vm.toJSON], { type:"application/json;charset=utf-8;" });			
		//var blob = new Blob([vm.newsThings], { type:"application/json;charset=utf-8;" });			
		var downloadLink = angular.element('<a></a>');
        downloadLink.attr('href',URL.createObjectURL(blob));
        downloadLink.attr('download', 'news.json');
		$log.debug(downloadLink);
		downloadLink[0].click();
	}
	*/
	
  }
})();

(function() {
  'use strict';

  angular
    .module('pasturaboscEditor')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, ngTranslation, $location) {
      ngTranslation.use(
        $location.search().lang
      );
    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('pasturaboscEditor')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('main', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
    ;

    $urlRouterProvider.otherwise('/');
  }

})();

/* global moment:false */
(function() {
  'use strict';

  angular
    .module('pasturaboscEditor')
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('pasturaboscEditor')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig, ngTranslationProvider) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
      
    ngTranslationProvider
      .setDirectory('app/i18n')
      .setFilesSuffix('.json')
      .langsFiles({
        en: 'en',
        fr: 'fr',
        es: 'es',
        ca: 'ca'
      })
      .fallbackLanguage('ca');
      
  }

})();

